import { Button } from 'antd-mobile';
import React from 'react';
import { useHistory } from 'react-router-dom';
import {
  AnswerList,
  AnswerListItem,
  AnswerResultList,
  ResultBg,
  ResultButtonBox,
  ResultTitle,
  TrueBox,
  TrueDesc,
  TrueDescItem,
  TruePercent,
  TrueTitle,
} from '../styles';

const Result: React.FC = () => {
  const history = useHistory();
  return (
    <>
      <ResultBg>
        <ResultTitle>练习报告</ResultTitle>
        <TrueBox>
          <TrueTitle>正确率</TrueTitle>
          <TruePercent>66%</TruePercent>
        </TrueBox>
        <TrueDesc>
          <TrueDescItem>1.1 微分系数</TrueDescItem>
          <TrueDescItem>用时 08：18</TrueDescItem>
        </TrueDesc>
      </ResultBg>
      <AnswerResultList>
        <AnswerList>
          <AnswerListItem className="true">1</AnswerListItem>
          <AnswerListItem className="error">2</AnswerListItem>
          <AnswerListItem className="true">3</AnswerListItem>
          <AnswerListItem className="error">4</AnswerListItem>
          <AnswerListItem className="true">5</AnswerListItem>
        </AnswerList>
      </AnswerResultList>
      <ResultButtonBox>
        <Button
          type="primary"
          inline
          onClick={(): void => history.push('/quick-parsing')}
        >
          查看解析
        </Button>
        <Button type="primary" inline onClick={(): void => history.go(-2)}>
          继续练习
        </Button>
      </ResultButtonBox>
    </>
  );
};

export default Result;
