import styled from 'styled-px2vw';
import pkg from '../../../../package.json';

const themeColor = pkg.theme['brand-primary'];
export const Content = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f8fffc;
  .slider-list,
  .slider-frame,
  .slider-list,
  .slider-slide {
    height: 100% !important;
  }
`;

export const HeaderBox = styled.div`
  height: 128px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 42px 0 30px;
  flex-shrink: 0;
  &.lr-pd {
    padding: 0 56px;
  }
`;

export const GrayFont = styled.div`
  color: #7f7f7f;
  font-weight: bold;
  font-size: 32px;
`;

export const FontContent = styled.div`
  line-height: 40px;
  color: #7f7f7f;
  margin-top: 46px;
  padding: 0 112px;
  word-break: break-all;
`;

export const AnswerBox = styled.div`
  padding: 98px 32px 0;
`;
export const AnswerItem = styled.div`
  height: 106px;
  line-height: 106px;
  border: 1px solid #e8e8e8;
  border-radius: 16px;
  color: #262626;
  font-weight: bold;
  margin-bottom: 44px;
  padding-left: 50px;
  &:last-child {
    margin-bottom: 0;
  }
  &.active {
    color: ${themeColor};
    background: #d7efe0;
    border-color: #d7efe0;
  }
  &.error {
    color: #f5222d;
    background-color: #f7c8c8;
    border-color: #f7c8c8;
  }
`;

export const CarouseBox = styled.div`
  height: 100%;
`;
export const CarouselItem = styled.div`
  height: 100%;
  overflow: auto;
`;

export const ButtonBox = styled.div`
  flex-shrink: 0;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom);
`;

export const ButtonBoxInner = styled.div`
  padding: 32px;
`;

// 结果页
export const ResultBg = styled.div`
  background: ${themeColor};
  border-radius: 0px 0px 15px 15px;
  min-height: 544px;
  font-weight: bold;
  color: #fff;
`;

export const ResultTitle = styled.div`
  padding-top: 40px;
  font-size: 42px;
  text-align: center;
`;

export const TrueBox = styled.div`
  width: 340px;
  height: 340px;
  border: 2px solid #ffffff;
  border-radius: 50%;
  margin: 34px auto 0;
  position: relative;
`;

export const TrueTitle = styled.div`
  font-size: 28px;
  text-align: center;
  margin-top: 42px;
`;

export const TruePercent = styled.div`
  font-size: 90px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

export const TrueDesc = styled.div`
  padding: 0 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;
export const TrueDescItem = styled.div`
  font-size: 32px;
`;

export const AnswerResultList = styled.div`
  padding: 0 74px;
`;

export const ResultButtonBox = styled.div`
  padding: 200px 34px 0;
  display: flex;
  justify-content: space-between;
`;
// 答题卡

export const AnswerModal = styled.div`
  height: 61.8vh;
  background: #fff;
  border-radius: 30px 30px 0px 0px;
  width: 750px;
`;
export const AnswerInner = styled.div`
  padding: 32px;
  height: 100%;
  display: flex;
  flex-direction: column;
`;
export const CloseBox = styled.div`
  position: absolute;
  right: 0;
  top: 0;
`;
export const AnswerTitle = styled.div`
  text-align: center;
  position: relative;
  height: 48px;
  line-height: 48px;
  font-size: 34px;
  flex-shrink: 0;
`;

export const AnswerListBox = styled.div`
  padding: 0 32px;
  height: 100%;
`;
export const AnswerLabel = styled.div`
  font-size: 32px;
  letter-spacing: 0.2px;
  color: #7f7f7f;
  padding-top: 64px;
`;

export const AnswerList = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 102px;
`;

export const AnswerListItem = styled.div`
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: #f5f5f5;
  color: #fff;
  text-align: center;
  line-height: 48px;
  &.true {
    background: ${themeColor};
  }
  &.error {
    background: #ffa39e;
  }
`;

// 解析页面
export const ParsingFont = styled.div`
  padding: 102px 100px 66px;
  font-size: 32px;
  line-height: 40px;
  letter-spacing: 0.4px;
  color: #7f7f7f;
  word-break: break-word;
  .title {
    margin-bottom: 50px;
  }
`;
